// MapController component to handle map references and dynamic updates
interface MapControllerProps {
  center: [number, number] | null;
  userLocation: [number, number] | null;
  onMapReady: (map: L.Map) => void;
  shouldRecenter: boolean;
  onRecenterComplete: () => void;
}

const MapController: React.FC<MapControllerProps> = ({ 
  center, 
  userLocation, 
  onMapReady, 
  shouldRecenter, 
  onRecenterComplete 
}) => {
  const map = useMap();

  // Initialize map reference
  useEffect(() => {
    if (map) {
      onMapReady(map);
    }
  }, [map, onMapReady]);

  // Handle dynamic recentering when user location updates
  useEffect(() => {
    if (map && userLocation && shouldRecenter) {
      try {
        // Calculate appropriate zoom level based on accuracy
        const currentZoom = map.getZoom();
        const targetZoom = currentZoom < 15 ? 17 : Math.max(currentZoom, 16);
        
        // Smooth animated pan to user location with dynamic zoom
        map.flyTo(userLocation, targetZoom, {
          animate: true,
          duration: 1.5,
          easeLinearity: 0.1,
          noMoveStart: false
        });
        
        // Visual feedback during transition
        console.log(`🗺️ Recentering map to user location: [${userLocation[0].toFixed(6)}, ${userLocation[1].toFixed(6)}]`);
        
        // Call completion callback after animation
        setTimeout(() => {
          onRecenterComplete();
        }, 1500);
      } catch (error) {
        console.warn('Map flyTo failed, using fallback:', error);
        try {
          // Fallback to immediate center update
          map.setView(userLocation, 17);
          onRecenterComplete();
        } catch (fallbackError) {
          console.error('Map fallback recentering failed:', fallbackError);
          onRecenterComplete();
        }
      }
    }
  }, [map, userLocation, shouldRecenter, onRecenterComplete]);

  // Handle click events for marker interactions and map updates
  useMapEvents({
    click: (e) => {
      console.log('Map clicked at:', e.latlng);
    },
    zoomend: () => {
      const currentZoom = map.getZoom();
      console.log('Zoom changed to:', currentZoom);
    },
    moveend: () => {
      const currentCenter = map.getCenter();
      console.log('Map moved to:', currentCenter);
    },
    resize: () => {
      // Handle responsive map resizing
      setTimeout(() => {
        map.invalidateSize();
      }, 100);
    }
  });

  // Responsive map sizing effect
  useEffect(() => {
    if (map) {
      const handleResize = () => {
        setTimeout(() => {
          map.invalidateSize();
        }, 100);
      };

      window.addEventListener('resize', handleResize);
      return () => window.removeEventListener('resize', handleResize);
    }
  }, [map]);

  return null;
};

// Performance monitoring utility
let frameCount = 0;
let lastFrameTime = performance.now();
let currentFPS = 60;

const updateFrameRate = () => {
  frameCount++;
  const now = performance.now();
  const delta = now - lastFrameTime;
  
  if (delta >= 1000) { // Update FPS every second
    currentFPS = Math.round((frameCount * 1000) / delta);
    frameCount = 0;
    lastFrameTime = now;
  }
  
  requestAnimationFrame(updateFrameRate);
};

// Start frame rate monitoring
requestAnimationFrame(updateFrameRate);

const monitorMapPerformance = (): PerformanceMetrics => {
  const performance = window.performance;
  const memoryInfo = (performance as any).memory;
  
  return {
    frameRate: currentFPS,
    memoryUsage: memoryInfo ? Math.round(memoryInfo.usedJSHeapSize / 1024 / 1024) : 0,
    renderTime: performance.now(),
    markerCount: document.querySelectorAll('.interactive-marker').length
  };
};

// Distance calculation utility
const calculateDistance = (lat1: number, lon1: number, lat2: number, lon2: number): number => {
  const R = 6371e3; // Earth's radius in meters
  const φ1 = lat1 * Math.PI / 180;
  const φ2 = lat2 * Math.PI / 180;
  const Δφ = (lat2 - lat1) * Math.PI / 180;
  const Δλ = (lon2 - lon1) * Math.PI / 180;

  const a = Math.sin(Δφ/2) * Math.sin(Δφ/2) +
          Math.cos(φ1) * Math.cos(φ2) *
          Math.sin(Δλ/2) * Math.sin(Δλ/2);
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));

  return R * c;
};

// Format distance for display
const formatDistance = (meters: number): string => {
  if (meters < 1000) {
    return `${Math.round(meters)}m`;
  } else {
    return `${(meters / 1000).toFixed(1)}km`;
  }
};

